<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
        <div class="part ct-left">
          <ctitle :title="'区划分布'"></ctitle>
          <div class="inner">
            <div
                v-if="barBackStatus"
                class="map-back"
                @click="barBack"
              ></div>
            <barline :params="qvfbBarParams"  @canvasClick='canvasClick'></barline>
          </div>
        </div>
        <div class="part ct-right">
          <ctitle :title="'信访形式'"></ctitle>
          <div class="inner">
            <pie-chart3D :params="zbPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="part ct-right">
          <ctitle :title="'信访目的'"></ctitle>
          <div class="inner">
            <loopPie class="chart" :params="rsgmzbPieParams"></loopPie>
          </div>
        </div>
      </div>
      <div class="c-btm">
        <div class="part">
          <ctitle :title="'信访量四级登记机构分布'"></ctitle>
          <div class="inner">
            <funnel class="inner-left" :params="zbFunnelParams"></funnel>
          </div>
        </div>
        <div class="part">
          <ctitle :title="'信访量四级登记机构分布走势'"></ctitle>
          <div class="inner">
            <LinesR
              class="chart"
              :params="zsLineParams"
            ></LinesR>
          </div>
        </div>
        <div class="part">
          <ctitle :title="'个访集体访'"></ctitle>
          <div class="inner">
            <loopPie
              class="chart"
              :params="zfxszbPieParams"
            ></loopPie>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
import ctitle from "@/components/title/index";
import loopPie from "@/components/pie/LoopPie";
import pictorialBar from "@/components/bar/pictorialBar";
import PieChart3D from "@/components/pie/PieChart3D";
import funnel from "@/components/funnel/Funnel";
import PerBar from "@/components/bar/PerBar";
import barline from "@/components/bar/BarLine1";
import LinesR from "@/components/line/LinesR";
import timeChange from "@/components/timeChange/index";

export default {
  name: "xfbm",
  components: {
    ctitle,
    loopPie,
    pictorialBar,
    PieChart3D,
    funnel,
    PerBar,
    barline,
    LinesR,
    timeChange
  },
  data() {
    return {
      mapJson: {
        广东省: "440000",
        广州市: "440100",
        韶关市: "440200",
        深圳市: "440300",
        珠海市: "440400",
        汕头市: "440500",
        佛山市: "440600",
        江门市: "440700",
        湛江市: "440800",
        茂名市: "440900",
        肇庆市: "441200",
        惠州市: "441300",
        梅州市: "441400",
        汕尾市: "441500",
        河源市: "441600",
        阳江市: "441700",
        清远市: "441800",
        东莞市: "441900",
        中山市: "442000",
        潮州市: "445100",
        揭阳市: "445200",
        云浮市: "445300"
      },
      barBackStatus: false,
      curCity: '广东省',
      shijCity: "",
      shej_02: "invalid",
      shij_02: "invalid",
      xj_02: "invalid",
      zsLineParams: {
        data: [],
        colors: [
          {
            line: "#e11770",
            start: "rgba(225, 23, 112, .5)",
            end: "rgba(225, 23, 112, .5)"
          },
          {
            line: "#ffb435",
            start: "rgba(255, 180, 53, .5)",
            end: "rgba(255, 180, 53, .5)"
          },
          {
            line: "#1a82f7",
            start: "rgba(26, 130, 247, .5)",
            end: "rgba(26, 130, 247, .5)"
          },
          {
            line: "#a1d871",
            start: "rgba(161, 216, 113, .5)",
            end: "rgba(161, 216, 113, .5)"
          },
          {
            line: "#ff825b",
            start: "rgba(255, 130, 91, .5)",
            end: "rgba(255, 130, 91, .5)"
          }
        ],
        option: {
          splitNumber: 4,
          splitLineShow: true,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisCategoryName: "年",
          // axisYName: "件次",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: true,
          areaShow: 0,
          smoothMonotone: "none",
          dataZoomshow: false,
          legendIcon: "rect",
          left: "8%",
          right: "15%",
          top: "15%",
          bottom: "10%",
          legendTop: "2%",
          legendRight: "15%"
        },
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        map: {},
        xName: "name"
      },

      // 区划分布
      qvfbBarParams: {
        data: [],
        canvasStyle: {
          width: "80%",
          height: "85%"
        },
        options: {
          xName: "name", //x轴字段
          axisValueShow: false,
          categoryBr: true,
          axis1Name: "人次",
          axisLabelSize: "12%", // label字号
          left: "5%",
          right: "5%",
          top: "0%",
          bottom: "0%",
          barLabelOptions: "top",
          barWidth: "35%", // 每个柱形图宽
          barBorderRadius: 5,
          splitLineShow: false,
          barLabelShow: false, //柱状图数量值显示
          axisLabelColor: "rgba(0, 176, 255, .8)",
          itemLabelColor: "rgba(0, 176, 255, .8)",
          colors: {
            bar: [["#fa71cd", "#c471f5"]]
          },
          map: {
            value: "数量"
          }
        }
      },
      // 信访量四级登记机构分布
      zbFunnelParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          colors: this.$STANDARD.gradientColors,
          tooltipShow: false,
          sort: "none",
          top: "10%",
          borderTop: "11%",
          rollFlag: true
        }
      },
      // 信访形式
      zbPieParams: {
        data: [],
        color: ["#655CDD", "#50B3FF", "#62EDAC"],

        angle: 20,
        radius: "35%",
        labelRadius: 40,
        styleObj: {
          width: "80%",
          height: "90%"
        },
        isBr: true,
        labelPer: true
      },
      curName: "部门索引",
      
      // 信访目的
      rsgmzbPieParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          colors: this.$STANDARD.gradientColors,
          radius: ["50%", "65%"],
          center: ["50%", "50%"],
          centerRadius: ["42%", "43%"],
          labelRich: {
            b: this.fontSize(0.2),
            d: this.fontSize(0.22),
            bHeight: this.fontSize(0.3)
          },
          length: 20,
          length2: 45,
          borderWidth: this.fontSize(0.05),
          bRichPadding: [30, -45, 0, -45],
          tooltip: {
            show: true
          },
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rich: {
            bfontsize: "14%" //文字大小
          },
          labelSize: "22%", // label字号
          rollFlag: true
        }
      },
      // 走访形式占比
      zfxszbPieParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          bgSize: 220,
          colors: this.$STANDARD.gradientColors,
          radius: ["45%", "55%"],
          center: ["50%", "50%"],
          centerRadius: ["40%", "41%"],
          labelRich: {
            b: this.fontSize(0.18),
            d: this.fontSize(0.2),
            bHeight: this.fontSize(0.3)
          },
          length: 20,
          length2: 70,
          // borderColor: "#050f28",
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rich: {
            bfontsize: "16%" //文字大小
          },
          labelSize: "24%", // label字号
          rollFlag: true,
          threeLevel: true
        }
      }
    };
  },
  mounted() {
    this.getAPIAndMap(this.date);
  },
  methods: {
    canvasClick(item) {
      this.barBackStatus = true;
      this.curCity = item.params.name;
      let obj={color:'#fefa82'}
      let c = ''
      for(let key in this.mapJson) {
        if (key === item.params.name) {
          c = key;
        }
      }
      if (!c) {
        this.qvfbBarParams.data.forEach((item,index)=>{
          if(this.curCity == item.name){
            this.$set(item, "itemStyle", obj)
          }else{
            this.$delete(item,"itemStyle");
          }
        })
      }
    },
    getQyfbAPI(date) {
      if (this.curCity === "广东省") {
        this.shej_02 = this.curCity;
        this.shij_02 = "invalid";
        this.xj_02 = "invalid";
        this.nameP = "shij_02";
      } else {
        // 市级
        if (this.shijCity) {
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.nameP = "xj_02";
          if (this.xj_02 !== 'invalid') {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
          } else {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
          }
        } else {
          this.shej_02 = "广东省";
          this.shijCity = 'invalid',
          this.xj_02 = 'invalid'
          this.nameP = "invalid";
        }
      }
      // 1.区划分布(柱状图)
      get_http("zdyztfxNctdzyQyfbBar", {
        day: `[${date.startDate},${date.endDate}]`,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
        name: this.nameP,
      })
      .then(res => {
        this.qvfbBarParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
    },
    getAPIAndMap(date) {
      this.getQyfbAPI(date)
      this.getAPI(date)
    },
    getAPI(date) {
      // 2.信访形式占比（饼图）
      get_http("zdyztfxNctdzyZbPie", {
        day: `[${date.startDate},${date.endDate}]`,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.zbPieParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      // 3.信访目的（环图）
      get_http("zdyztfxNctdzyRsgmzbPie", {
        day: `[${date.startDate},${date.endDate}]`,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.rsgmzbPieParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });

      // 4.登记机构占比（漏斗图）
      get_http("zdyztfxNctdzyZbFunnel", {
        day: `[${date.startDate},${date.endDate}]`,
        Cqh: this.curCity
        // shej_02: this.shej_02,
        // shij_02: this.shij_02,
        // xj_02: this.xj_02,
      })
      .then(res => {
        this.zbFunnelParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      // 5.信访量四级登记机构分布走势
      get_http("zdyztfxNctdzyZsLine", { 
        day: `[${date.startDate},${date.endDate}]`,
        Cqh: this.curCity
        // shej_02: this.shej_02,
        // shij_02: this.shij_02,
        // xj_02: this.xj_02,
      })
      .then(res => {
        this.zsLineParams.option.axisCategoryName = '日'
        this.zsLineParams.map = res.data.map;
        this.zsLineParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
      // 6.个访集体访（环图）
      get_http("zdyztfxNctdzyZfxszbPie", {
        day: `[${date.startDate},${date.endDate}]`,
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
      })
      .then(res => {
        this.zfxszbPieParams.data = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
    },
    fontSize(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    },
    // 地图返回全省
    barBack() {
      this.barBackStatus = false;
      this.curCity = "广东省";
    },
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    date(val) {
      this.getAPIAndMap(val);
    },
    curCity: function(newVal, oldVal) {
      if (newVal === '广东省') {
        this.getAPIAndMap(this.date)
      } else {
        let c = "";
        for (let key in this.mapJson) {
          if (newVal === key) {
            this.shijCity = newVal;
            c = newVal;
            this.getAPIAndMap(this.date);
          }
        }
        if (!c) {
          this.xj_02 = newVal;
          this.getAPI(this.date);
        }
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.inner
  width 100%
.wrapper
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)
  .fade-enter-active, .fade-leave-active
    transition opacity .3s
  .fade-enter, .fade-leave-to
    opacity 0
  .w-seach
    position absolute
    top -78px
    right 113px
    z-index 1000
    box-sizing border-box
    padding-left 42px
    width 262px
    line-height 62px
    font-size 22px
    color #cbfbff
    background url("../../../assets/seach-bg.png") no-repeat
    cursor pointer
    p
      width 150px
      text-align center
      white-space nowrap
      overflow hidden
      text-overflow ellipsis
  .content
    position relative
    z-index 1
    width 100%
    height 100%
    display flex
    flex-direction column
    padding .2rem .3rem
    box-sizing border-box
    .part
      display flex
      flex-direction column
      width 100%
      height 100%
      .inner
        display flex
        justify-content center
        height calc(100% - 0.28rem)
        .chart
          width 100%
          height 100%
    .c-top
      flex 1
      display flex
      .ct-left
        flex 1.5
        .inner
          position relative
          .map-back
            position absolute
            background url("~@/assets/back-icon.png") no-repeat
            top 30px
            right 60px
            width 30px
            height 30px
            z-index 1000
            font-size 0.2rem
            color #fff
            cursor pointer
      .ct-right
        flex 1
    .c-btm
      flex 1
      display flex
      .part
        flex 1
        .inner
          position relative
          .inner-left
            margin-left 1rem
          .timeChange
            top -0.3rem
            right 0.9rem
</style>
